<template>
  <div class="agree full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="deal">
          <h1>财务顾问服务合作协议（个人版）</h1>

          <p class="nopadding">甲方：清科管理顾问集团有限公司</p>
          <p>法定代表人：倪正东</p>
          <p class="nopadding">乙方：</p>
          <p>身份证号码：</p>
          <p class="fBlod">鉴于：</p>
          <p class="txtindent">甲方系运营“项目工场”股权投融资平台（www.pemarket.com.cn，“项目工场平台”）的公司，乙方系在项目工场平台注册，拟与甲方进行合作，利用项目工场平台资源促成投融资交易的自然人财务顾问。</p>
          <p class="txtindent">双方本着自愿、平等、互惠互利、诚实信用的原则，经充分友好协商，就财务顾问服务合作相关事宜达成如下协议：</p>
          
          <h2>1．合作内容</h2>
          <p>双方的合作模式包括但不限于：</p>
          <p>1.1 甲方通过项目工场平台向乙方推荐具有投资、融资需求的项目方（包括投资方或融资方，统称“项目方”），乙方通过在项目工场平台上注册并认领项目，为项目方提供财务顾问服务，最终促成投融资交易完成。</p>
          <p>1.2 乙方向甲方推荐具有投资、融资需求的项目方，经甲方审核、筛选通过后在项目工场平台上发布项目，甲方为项目方匹配所需资源，乙方为项目方提供财务顾问服务，最终促成投融资交易完成。</p>
          <p>1.3 乙方向甲方推荐具有投资、融资需求的项目方，经甲方审核、筛选通过后在项目工场平台上发布项目，平台宣传、推广后，乙方通过自身优势寻找与项目相匹配的资源，最终促成投融资交易完成。</p>

          <h2>2．交易的定义</h2>
          <p>本协议中所述的交易，包括但不限于以下三种情形：</p>
          <p>2.1通过项目工场平台提供的相关服务，促使融资方及其关联公司完成投资和融资，包括现金投融资、有价证券以及其他任何可转换或兑换的公司债券或者股权的投融资。</p>
          <p>2.2通过项目工场平台提供的相关服务，促使投资方以股权收购或者资产收购的方式购买融资方及其关联公司的部分或者全部股权或资产，使得投资方获得融资方或其关联公司的控制权。</p>
          <p>2.3通过项目工场平台提供的相关服务，促使融资方及其关联公司与投资方通过合并的方式组成一家企业，融资方及其关联公司被投资方所吸收合并。</p>

          <h2>3．双方的权利义务</h2>
          <p>3.1本协议任何一方推荐的项目方均应是依据中国法律设立并有效存续的或具有完全民事行为能力的公司、其他组织或自然人，具有一切必要的权利及能力履行本协议项下的所有义务及项目工场平台的行为规则，但本协议任何一方均不对另一方就交易的达成作出任何担保或保证。</p>
          <p>3.2推荐项目的一方应当在合理期间内向另一方提供为履行本协议所需的项目方相关资料（包括但不限于公司业务、市场、战略、团队、财务等方面的相关资料），并应尽其最大努力确认该等资料的真实、准确或完整性。</p>
          <p>3.3项目工场平台发布项目后，乙方应利用其自身优势为项目方提供相应的投资、融资业务指导，主要包括以下一项或几项：</p>
          <p>3.3.1指导或协助项目方完成商业计划书；</p>
          <p>3.3.2为项目方寻找目标投资方/融资方；</p>
          <p>3.3.3协助项目方与投资方/融资方建立联系，进行必要的沟通；</p>
          <p>3.3.4安排、协助项目方进行谈判及签订投资或并购意向书（Term Sheet）。</p>
          <p>3.4乙方应先在项目工场平台上完成注册，参加甲方组织的财务顾问面试，通过面试后方可在项目工场平台申请认领项目、推荐项目。乙方除遵守平台用户协议外，还须遵守以下规则：</p>
          <p>3.4.1乙方每周最多可认领2个项目，总计最多同时认领5个项目；</p>
          <p>3.4.2乙方对于其认领成功的项目应在“工作台-待约谈”中根据项目进度进行调整工作流、添加备注等操作，超过30天未操作，系统将自动取消其认领的该项目；</p>
          <p>3.4.3乙方对于其认领成功的项目应在“工作台-跟进”中根据项目进度进行调整工作流、未添加备注等操作，超过90天未操作，系统自动取消其认领的该项目。</p>
          <p class="fBlod">3.5 在本协议第1条约定的任一合作模式下，均应由甲方与项目方签署《财务顾问服务协议》，乙方不得以自身名义与项目方签署与本协议内容有抵触的或相似的财务顾问服务协议，或与项目方实质性地达成与本协议内容有冲突或抵触的其他安排。</p>
          <p>3.6 未经项目方及项目推荐方书面许可，本协议任何一方不得泄漏由于履行本协议而知悉的项目方及项目推荐方商业秘密，否则，泄密一方须承担因泄露商业秘密所产生的损失。</p>
          <p>3.6 未经项目方及项目推荐方书面许可，本协议任何一方不得泄漏由于履行本协议而知悉的项目方及项目推荐方商业秘密，否则，泄密一方须承担因泄露商业秘密所产生的损失。</p>

          <h2>4．服务费用</h2>
          <p>4.1在项目方成功并购或获得融资并将财务顾问服务费支付给甲方后，甲方应将其所收到的服务费扣除应缴纳增值税及附加后按一定比例支付给乙方，具体分为以下几种情况：</p>
          <p>4.1.1 在本协议第1.1条约定的合作模式下，甲方将其从项目方实际收到的财务顾问服务费扣除应缴纳增值税及附加后的70%支付给乙方；</p>
          <p>4.1.2 在本协议第1.2条约定的合作模式下，甲方将其从项目方实际收到的财务顾问服务费扣除应缴纳增值税及附加后的80%支付给乙方；</p>
          <p>4.1.3 在本协议第1.3条约定的合作模式下，甲方将其从项目方实际收到的财务顾问服务费扣除应缴纳增值税及附加后的90%支付给乙方。</p>
          <p>自本协议终止之日起[6]个月内完成的交易，甲方在收到项目方支付的财务顾问服务费后仍应按照上述比例向乙方支付。</p>
          <p>乙方不可撤销地确认其具有完全民事能力及行为能力，同意甲方按照现行及届时有效的个人所得税法律法规及政策为乙方在本协议项下的收入进行代扣代缴个人所得税。</p>
          <p>4.2对于前述约定的财务顾问服务费，甲方应当自收到项目方支付的财务顾问服务费（扣除应缴纳增值税及附加）之日起[30]日内一次性将财务顾问服务费支付至乙方指定的如下账户：</p>
          <p>账户名称：</p>
          <p>开户行：</p>
          <p>账号：</p>
          <p>4.3 双方各自因提供财务顾问服务而产生的税费、交通费、住宿费、材料费、尽职调查费以及其他发生合理的费用，由双方各自承担。</p>

          <h2>5．协议期限及终止</h2>
          <p>本协议自双方签字盖章之日起生效，有效期为[12]个月。在有效期内，协议双方均不得无故单方解除本协议。若乙方因违反项目工场平台规则而被甲方注销账户，则本协议随即终止。协议终止后，双方均不再向对方履行义务，但仍应就协议终止前的事项按照协议安排向对方承担责任。</p>

          <h2>6．法律关系</h2>
          <p>协议双方确认在本协议下，双方仅限于项目资源共享及对项目方提供财务顾问服务进行合作。不因此而产生任何诸如劳动雇佣、代理、分销等法律关系。</p>

          <h2>7．违约责任</h2>
          <p>7.1本协议生效后，甲乙双方均应诚实信用地履行约定。任何一方违反约定，给守约方造成经济损失的，违约方应向守约方就其遭受的实际经济损失进行赔偿。法律另有规定或者本协议另有约定的除外。</p>
          <p>7.2若乙方以自身名义与项目方签署与本协议内容有抵触的或相似的财务顾问服务协议，或与项目方实质性地达成了与本协议内容有冲突或抵触的其他安排，则甲方有权单方面终止本协议，注销乙方在项目工场平台上的账户，并要求乙方赔偿经济损失。</p>

          <h2>8．法律适用及争议的解决</h2>
          <p>本协议的订立、效力、解释、履行和争议的解决，均受中华人民共和国法律的管辖。本协议的修改及变更必须经甲乙双方协商同意并签署书面协议后方能生效。如果发生争执，双方应协商解决，协商未成的，任何一方有权将该争议提交北京仲裁委员会进行仲裁，仲裁裁决是终局的，对双方均具有约束力。</p>

          <h2>9．其他</h2>
          <p>9.1本协议一式两份，协议双方各持一份，具有同等法律效力。</p>
          <p>9.2本协议未尽事宜或后续有变更事宜（如乙方签约主体由自然人变更为法人），由甲、乙双方协商，以书面形式签订补充协议。补充协议为本协议不可分割的一部分，与本协议具有同等法律效力。</p>

          <p class="lastnote">（本页无正文，为签署页）</p>

          <p>
            <span class="bleft">甲方：清科管理顾问集团有限公司 </span>
            <span class="bright">乙方：</span>
          </p>
          <p>法定代表人：倪正东</p>
          <p>
            <span class="bleft">盖章： </span>
            <span class="bright">签字：</span>
          </p>
          <p>
            <span class="bleft">日期：2018年  月  日 </span>
            <span class="bright">日期：2018年  月  日</span>
          </p>

          <h2></h2>
          <p></p>

        </div>
      </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemScrollView
  },
  created () {
    this._setTitle('财务顾问服务合作协议（个人版）')
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .agree
    font-size: 0.24rem
    background-color: #ffffff
    .page-panel
      .deal
        padding: 0 0.3rem
        background-color: #ffffff
        .nopadding
          padding-bottom: 0
        .txtindent
          text-indent: 2em 
        .lastnote
          text-align: center
          padding: 0.3rem 0
        .bleft,
        .bright
          display: inline-block
          vertical-align: top
          width: 48%
        .bright
          padding-left: 2%
          -webkit-box-sizing: border-box
          -moz-box-sizing: border-box 
          box-sizing: border-box
        h1
          padding: 0.64rem 0 0.2rem 0
          font-weight: bold
          font-size: 0.34rem
          color: $color-text-title
          text-align: center
          letter-spacing: -0.0082px
          line-height: 0.48rem
        h2
          padding: 0.28rem 0 0.24rem 0
          font-weight: bold
          font-size: 0.3rem
          color: $color-text-body
          letter-spacing: -0.003rem
          line-height: 0.4rem
        p
          padding-bottom: 0.24rem
          text-align: justify
          font-size: 0.28rem
          color: $color-text-body
          letter-spacing: -0.003rem
          line-height: 0.4rem
          &.fBlod
            font-weight: bold
            color: $color-text-title

</style>



